import React, { Component, useState, useEffect } from "react";
import { Card, Container, Row, Col, Button } from "react-bootstrap";
import { WebNav } from "../../components/navbar";
import { CoverHalf } from "../../components/coverHalf";
import { Footer } from '../../components/footer';

import peopleData from "./peopleData";

function People(props) {

  return (
    <React.StrictMode>
      <WebNav />
      <CoverHalf />
      <CurrentMember />
      <Footer />
    </React.StrictMode>

  );
}

export { People };





function Section(props) {
  let people = props.data



  return (
    people.map(member => (
      <Col xs={12} md={3}>
        <IntroductionCard img={member.image} name={member.name} text={member.description} />
      </Col>
    )))
}

function IntroductionCard(props) {


  return (
    <Card className='my-3 mx-1'>
      <Card.Img variant="top" src={props.img} />
      <Card.Body>
        <Card.Title>{props.name}</Card.Title>
        <Card.Text style={{ minHeight: '1.2rem' }}>{props.text}</Card.Text>
      </Card.Body>
    </Card>
  );
}

function CurrentMember() {
  return (
    <Container>
      <Row>
        <div className='people-section text-center w-100'>
          <div className='title' style={{ fontSize: '1.5rem', letterSpacing: '0.3rem', fontWeight: '500' }}>
            <p>研究員  Lab Researchers</p>
            <hr />
          </div>
        </div>
      </Row>
      <Row>
        <Section data={peopleData.currentMember.labResearchers} />
      </Row>

      <Row>
        <div className='people-section text-center w-100'>
          <div className='title' style={{ fontSize: '1.5rem', letterSpacing: '0.3rem', fontWeight: '500' }}>
            <p>碩士生  Graduate Students</p>
            <hr />
          </div>
        </div>
      </Row>
      <Row>
        <Section data={peopleData.currentMember.graduateStudents} />
      </Row>

      <Row>
        <div className='people-section text-center w-100'>
          <div className='title' style={{ fontSize: '1.5rem', letterSpacing: '0.3rem', fontWeight: '500' }}>
            <p>專題生  Undergraduate Students</p>
            <hr />
          </div>
        </div>
      </Row>
      <Row>
        <Section data={peopleData.currentMember.undergraduateStudents} />
      </Row>
    </Container>
  )
}
